import React, { useState } from "react";
import "./CallContainer.scss";
import MyModal from "../Modal/MyModal";
import PersonsCard from "../PersonsCard/PersonsCard";
function CallContainer({
  dcontacts,
  setdcontacts,
  showModal,
  showCardModal,
  modalUser,
  setshowModal,
  setmodalUser,
  favoriteCheck,
}) {
  const [search, setsearch] = useState("");
  const searchHandleChange = (event) => {
    const { value } = event.target;
    setsearch(value);
  };

  const Contacts = dcontacts;
  let filterd = Contacts.filter(
    (item) =>
      item.lastname.toLowerCase().startsWith(search) ||
      item.phone.startsWith(search) ||
      item.name.toLowerCase().startsWith(search)
  );
  const numberAddToInput = (event) => {
    setnumberInput(numberInput.concat(event.target.id));
    setsearch(numberInput.concat(event.target.id));
  };
  const deletLastNumberFromInput = () => {
    setnumberInput(numberInput.substring(0, numberInput.length - 1));
    setsearch(numberInput.substring(0, numberInput.length - 1));
  };
  const contactNumberImportToInput = (item) => {
    setnumberInput(item.phone);
    setsearch("");
  };
  const [numberInput, setnumberInput] = useState("");
  return (
    <>
      <div className="callContainerdiv">
        <div className="serachdiv">
          <input
            className="callSearchInput"
            onChange={searchHandleChange}
            type="text"
            placeholder="search your contact"
          />
        </div>

        <div className="mySearchShow">
          {search !== "" &&
            filterd.map((item) => (
              <div onClick={() => contactNumberImportToInput(item)}>
                <PersonsCard
                  key={item.id}
                  Contacts={item}
                  showCardModal={() => {}}
                  favoriteCheck={() => favoriteCheck(item)}
                />
              </div>
            ))}
        </div>

        <div className="tablediv">
          <div className="inputandbackspacediv">
            <div className="inputdiv">
              <input value={numberInput} type="text" />
            </div>
            <div
              className="backspacediv"
              onClick={() => {
                deletLastNumberFromInput();
              }}
            >
              <i class="fas fa-backspace"></i>
            </div>
          </div>
          <table>
            <tr>
              <td id="1" onClick={(e) => numberAddToInput(e)}>
                1
              </td>
              <td id="2" onClick={(e) => numberAddToInput(e)}>
                2
              </td>
              <td id="3" onClick={(e) => numberAddToInput(e)}>
                3
              </td>
            </tr>
            <tr>
              <td id="4" onClick={(e) => numberAddToInput(e)}>
                4
              </td>
              <td id="5" onClick={(e) => numberAddToInput(e)}>
                5
              </td>
              <td id="6" onClick={(e) => numberAddToInput(e)}>
                6
              </td>
            </tr>
            <tr>
              <td id="7" onClick={(e) => numberAddToInput(e)}>
                7
              </td>
              <td id="8" onClick={(e) => numberAddToInput(e)}>
                8
              </td>
              <td id="9" onClick={(e) => numberAddToInput(e)}>
                9
              </td>
            </tr>
            <tr>
              <td id="#" onClick={(e) => numberAddToInput(e)}>
                #
              </td>
              <td id="0" onClick={(e) => numberAddToInput(e)}>
                0
              </td>
              <td id="*" onClick={(e) => numberAddToInput(e)}>
                *
              </td>
            </tr>
          </table>
        </div>
        <div className="callButtonDiv">
          <a href={`tel:${numberInput}`} className="callButtonInnerDiv">
            <i class="fas fa-phone"></i>
          </a>
        </div>
      </div>
      <MyModal
        dcontacts={dcontacts}
        setdcontacts={setdcontacts}
        showModal={showModal}
        setshowModal={() => setshowModal()}
        modalUser={modalUser}
        setmodalUser={() => setmodalUser()}
      />
    </>
  );
}

export default CallContainer;
